a fora do futebol sertanejo. O clube tambm j disputou a\n        <strong>Srie B do\n          Campeonato Brasileiro</strong>, ampliando sua presena no cenrio nacional e conquistando o respeito de\n        torcedores e\n        adversrios.\n        <br><br>\n        Manda seus jogos no <strong>Estdio Cornlio de Barros</strong>, um verdadeiro <strong>templo do futebol\n          sertanejo</strong>, onde sua torcida fiel \n        conhecida por sua <strong>garra e calor humano</strong>  transforma cada partida em uma festa. O Salgueiro no\n         apenas o time da\n        cidade de Salgueiro;  <strong>o time de todo o Serto Nordestino</strong>, representando a <strong>resilincia,\n          a determinao e o esprito\n          guerreiro</strong> de seu povo.\n        <br><br>\n        Revelando talentos e escrevendo sua trajetria com <strong>ousadia e amor  camisa</strong>, o Salgueiro\n        Atltico Clube segue inspirando\n        geraes, provando que, no futebol como na vida, <strong>o sertanejo no desiste nunca. SALGUEIRO, O TIME DO\n          NORDESTE!</strong>\n      </p>\n    </div>\n  </div>\n  \n</template>\n<style scoped>\n.has-elipsis {\n  position: relative;\n}\n\n.has-elipsis::before {\n  content: '';\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  left: 0;\n  top: 0;\n  background: rgba(7, 116, 156, 0.62);\n  mix-blend-mode: hard-light;\n  filter: blur(97.5px);\n}\n\n.section-title {\n  margin-bottom: 4rem;\n  font-weight: 900;\n  font-size: 3.5rem;\n  text-align: center;\n  color: #EEE;\n}\n\n.historia {\n  position: relative;\n  padding: 5rem 6rem;\n  background: linear-gradient(80.65deg, rgba(19, 55, 128, .2) 5.25%, rgba(35, 88, 193, .2) 100.74%);\n  border-radius: 2rem;\n}\n</style>"],"sourceRoot":""}]);
